<template>
      <v-layout row>
         <v-flex  xs12>
                <div  class="pricing-wrapper clearfix">
                  <div class="d-flex justify-space-between">
                    <v-subheader>Plans <v-chip >{{plans.length}}</v-chip></v-subheader>
                </div>
                  <div 
                    v-for="(subscription,index) in plans"
                   
                    >
                    <div 
                    :index="index"
                    :key="subscription.id"
                    
                    >
                 
                  <div class="pricing-table">
                       <h3 class="pricing-title">{{subscription.package_name}}</h3>
                       <div class="price">N {{subscription.price}}</div>
                       <!-- Lista de Caracteristicas / Propiedades -->
                <ul class="table-list">
                    <li>{{subscription.quantity_slot}}  <span>Slots</span></li>
                    <li>{{subscription.duration_days}} days <span>Duration</span></li>
                
                </ul>
                <!-- Contratar / Comprar -->
                <div class="table-buy">
                  <p>N {{subscription.price}}</p>

                   <a href="#" class="pricing-action"  @click="viewPlan(subscription.id)">ORDER</a>
                  </div>
              </div>

                 <!-- <v-card  style="border-left: red solid thick" class="pa-2 mb-1">
                     <div class="subheading font-weight-bold py-2"> 
                                    {{subscription.package_name}}
                      </div>
                      <v-layout row wrap justify-end>
                          <v-flex xs6>
                              <div>
                                  <span>Duration: {{subscription.duration_days}}  days</span><br>
                                  <span>Slots: {{subscription.quantity_slot}}</span><br>
                                  <span class="red--text">Price : N {{subscription.price}}</span>
                              </div>
                          </v-flex>
                          <v-flex xs6 align-end>
                              <v-btn color="success" outline @click="viewPlan(subscription.id)">ORDER</v-btn>
                          </v-flex>
                      </v-layout>
                  </v-card> -->
                  
                  </div>
                </div>
                </div>
              
          </v-flex> <!-- End template for mobile display -->
         
      </v-layout>
</template>

<script>
import store from './store' 
import HandlesRequest from "@/mixins/RequestHandler"
export default { 
    data(){
        return {
            subscriptionList: []
        }
    },
    computed:{
        plans(){
            // return this.subscriptionList
            return this.$store.getters["dashboard_store/planList"]
        }
    },
    mixins: [ HandlesRequest ],
    created(){

        this.loadPlans()
    },
    methods:{
        loadPlans(){
            //load packages
            
             this.mixin_handleRequest(this.$store.dispatch("dashboard_store/retrievePlans"))
        },

        viewPlan(planID){
         
            this.$router.push({name: 'PackageView', params:{planID}})

        }
    }
}
</script>

<style scoped>
 * {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    background: #2e2a2a;
    color: #FFF;
    font-size: 62.5%;
    font-family: 'Roboto', Arial, Helvetica, Sans-serif, Verdana;
}

ul {
    list-style-type: none;
}

a {
    color: #e95846;
    text-decoration: none;
}

.pricing-table-title {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2.6em;
    color: #FFF;
    margin-top: 15px;
    text-align: left;
    margin-bottom: 25px;
    text-shadow: 0 1px 1px rgba(0,0,0,0.4);
}

.pricing-table-title a {
    font-size: 0.6em;
}

.clearfix:after {
    content: '';
    display: block;
    height: 0;
    width: 0;
    clear: both;
}
/** ========================
 * Contenedor
 ============================*/
.pricing-wrapper {
    width: 960px;
    margin: 40px auto 0;
}

.pricing-table {
    margin: 0 10px;
    margin-bottom: 10px;
    text-align: center;
    width: 300px;
    float: left;
    -webkit-box-shadow: 0 0 15px rgba(0,0,0,0.4);
    box-shadow: 0 0 15px rgba(0,0,0,0.4);
    -webkit-transition: all 0.25s ease;
    -o-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

.pricing-table:hover {
    -webkit-transform: scale(1.06);
    -ms-transform: scale(1.06);
    -o-transform: scale(1.06);
    transform: scale(1.06);
}

.pricing-title {
    color: #FFF;
    background: #e95846;
    padding: 20px 0;
    font-size: 2em;
    text-transform: uppercase;
    text-shadow: 0 1px 1px rgba(0,0,0,0.4);
}

.pricing-table.recommended .pricing-title {
    background: #2db3cb;
}

.pricing-table.recommended .pricing-action {
    background: #2db3cb;
}

.pricing-table .price {
    background: #403e3d;
    font-size: 3.4em;
    font-weight: 700;
    padding: 20px 0;
    text-shadow: 0 1px 1px rgba(0,0,0,0.4);
    color:#fcf7f7e6;
}

.pricing-table .price sup {
    font-size: 0.4em;
    position: relative;
    left: 5px;
}

.table-list {
    background: #FFF;
    color: #403d3a;
}

.table-list li {
    font-size: 1.4em;
    font-weight: 700;
    padding: 12px 8px;
}

.table-list li:before {
    content: "\e5ca";
    font-family: 'Material Icons';
    color: #3fab91;
    display: inline-block;
    position: relative;
    right: 5px;
    font-size: 16px;
} 

.table-list li span {
    font-weight: 400;
}

.table-list li span.unlimited {
    color: #FFF;
    background: #e95846;
    font-size: 0.9em;
    padding: 5px 7px;
    display: inline-block;
    -webkit-border-radius: 38px;
    -moz-border-radius: 38px;
    border-radius: 38px;
}


.table-list li:nth-child(2n) {
    background: #F0F0F0;
}

.table-buy {
    background: #FFF;
    padding: 15px;
    text-align: left;
    overflow: hidden;
}

.table-buy p {
    float: left;
    color: #37353a;
    font-weight: 700;
    font-size: 2.4em;
}

.table-buy p sup {
    font-size: 0.5em;
    position: relative;
    left: 5px;
}

.table-buy .pricing-action {
    float: right;
    color: #FFF;
    background: #e95846;
    padding: 10px 16px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    font-weight: 700;
    font-size: 1.4em;
    text-shadow: 0 1px 1px rgba(0,0,0,0.4);
    -webkit-transition: all 0.25s ease;
    -o-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

.table-buy .pricing-action:hover {
    background: #cf4f3e;
}

.recommended .table-buy .pricing-action:hover {
    background: #228799;    
}

/** ================
 * Responsive
 ===================*/
 @media only screen and (min-width: 768px) and (max-width: 959px) {
    .pricing-wrapper {
        width: 768px;
    }

    .pricing-table {
        width: 236px;
    }
    
    .table-list li {
        font-size: 1.3em;
    }

 }

 @media only screen and (max-width: 767px) {
    .pricing-wrapper {
        width: 420px;
    }

    .pricing-table {
        display: block;
        float: none;
        margin: 0 0 20px 0;
        width: 100%;
    }
 }

@media only screen and (max-width: 479px) {
    .pricing-wrapper {
        width: 300px;
    }
} 
</style>



